var render = function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"designsystems"},[_c('v-container',[_c('h1',{staticClass:"text-center display-2 mb-10"},[_vm._v("Front-End Design Systems")]),_c('h3',[_vm._v(":= How to implement UI & UX for desktop and mobile")]),_c('v-row',[_c('v-col',[_c('h3',[_vm._v("Google Material Design Guidelines")]),_c('ul',[_vm._v("Principles"),_c('li',[_vm._v("Layout: predictable, consistent, responsive")]),_c('li',[_vm._v("Sound: informative, honest, reassuring")]),_c('li',[_vm._v("Motion: informative, focused, expressive")]),_c('li',[_vm._v("Gestures: alternative interaction, easy to use, tactile control")])]),_c('br'),_c('ul',[_c('li',[_vm._v("Imitate real life physics → shadow")])]),_c('v-icon',[_vm._v("mdi-vuetify")]),_c('span',[_vm._v("Vuetify (Material Component Framework)")]),_c('br'),_c('br'),_c('p',[_vm._v("→ Material Design with Vuetify")])],1),_c('v-col',[_c('h3',[_vm._v("Apple Design Guidelines")]),_c('ul',[_c('li',[_vm._v("Guidelines: clarity, deference, depth ")]),_c('li',[_vm._v("Principles:"),_c('ul',[_c('li',[_vm._v("aesthetic ")]),_c('li',[_vm._v("integrity")]),_c('li',[_vm._v("consistency")]),_c('li',[_vm._v("direct manipulation")]),_c('li',[_vm._v("feedback")]),_c('li',[_vm._v("metaphors")]),_c('li',[_vm._v("user control")])])]),_c('li',[_vm._v("Not detailed → application in project unclear ")])])])],1),_c('div',{staticClass:"block"}),_c('v-row',[_c('p',[_vm._v("The significance of the UI and UX in web development is rising because UI and UX are essential to attain and keep the attention of users (Yi et al., 2018). For example, a great UX comprises well-thought-out features. Front-end design languages aid in the development of websites with a high level of usability and appearance, for instance, they define how these features are implemented and integrated into a website."),_c('br'),_vm._v("According to Ian Grout, design languages “provide the means by which to describe the operation of both software programs and hardware” (Grout, 2008). If this definition is applied and narrowed down to the front-end, front-end design languages specify how the characteristics and features of the UI and UX should be implemented (Baldwin, n.d.). In the context of the “Game of Competences” seminar, a front-end design language is advised to be used since it includes principles to enhance the appearance and usability of a website, for example, it minimizes friction by displaying an uncluttered UI and fosters flawless navigation of a website."),_c('br'),_vm._v("There are various approaches to design languages by different companies, for instance, Fluent Design Systems by Microsoft, Lighting Design System by Salesforce, Polaris by Shopify and Living Language by IBM. Hence, only the two most influential ones will be presented (Chandan & Varun, n.d.).")])]),_c('v-row',[_c('v-col',[_c('h3',[_vm._v("Material Design")]),_c('p',[_vm._v("Google’s Material Theme offers a comprehensive and very detailed rulebook to desktop and mobile design. The key idea is to imitate real-life physics, e.g. shadow is cast by key and ambient light and altered by elevation to the underlying material instead of changing the use of color. Additionally, user input may alter the level of elevation so that “components move to preset dynamic elevation offset“ (Google, n.d.-c, n.d.-d) and a three-dimensional effect is achieved."),_c('br'),_vm._v("The material layout uses three principles: Firstly, the layout needs to be predictable. Secondly, there needs to be a consistency in design in form of a repeating grid, harmonious keylines and padding. Thirdly, responsiveness is essential because mobile and desktop devices are heavily used by consumers. The goal of responsive design is guaranteed by the use of columns (cf. 1 in Figure 2), gutters (cf. 2 in Figure 2) and margins (cf. 3 in Figure 2) (Google, n.d.-h)."),_c('br'),_vm._v("Furthermore, it is stressed that sound needs to be informative, honest and reassuring. Thus, intuitiveness, functionality, understandability and authenticity are provided and an atmosphere of comfort and security is created (Google, n.d.-b). "),_c('br'),_vm._v("Moreover, further details are highlighted in the guidelines about navigation and parameters for choosing a color palette (Material Design, n.d.; Understanding Navigation - Material Design, n.d.). Lastly, Google offers a type scale generator and explains how sound, icons, shape and motion are defined and used in Material Design system (Google, n.d.-g, n.d.-b, n.d.-f, n.d.-a, n.d.-i). "),_c('br'),_vm._v("The Material Design system can be used with the front-end library Materialize.css and the Material Design framework Vuetify, which was specifically developed for Vue. Hence, it offers a fast implementation of the Material Design guidelines and is simple to learn and apply as demonstrated in the appendix."),_c('br'),_vm._v("In conclusion, it is recommended to use the Material Theme for this seminar, because of its specific and detailed guidelines and its fast and simplistic implementation using Vuetify.")])]),_c('v-col',[_c('h3',[_vm._v("Apple Design Guidelines")]),_c('p',[_vm._v("Apple’s design guidelines use rules of clarity, deference and depth for their themes. In detail, that accentuates that text is readably and components are displayed cleanly. The use of blur and the minimalistic approach to gradients and shadows have an attentive effect and create depth and a layered UI."),_c('br'),_vm._v("Apple’s design principles lead to a sophisticated presentation of its brand. First, they comprise aesthetic integrity, which underlines how UI impression and behavior replenish and enrich its functionality. Second, there is a focus on consistency, e.g. the use of familiar icons, interface components and unequivocal terminology. Third, direct manipulation involves the user in interaction. Hence, UX is enhanced through device rotation and gestures. Fourth, feedback through progress bars or hover and click effects keeps the user engaged. Fifth, metaphors spark interest by providing real-life concepts, for instance, toggle switches, drag and drop content or digital pages of books. Sixth, user control is provided by guiding the user, who ultimately decides (Apple, n.d.-b, n.d.-a).")])])],1),_c('h2',[_vm._v("Design Principles and Theory")]),_c('p',[_vm._v("There are various types of principles. Hence, an exemplary principle is presented.Standardization of terminology (for use cases) facilitates the development and reduces the frequency and extent of the impact of misunderstandings (Tena et al., 2013). This idea may be observed in the predefined methods, e.g. the functionality of the filter method is commonly known. Additionally, this concept is applied in Vuetify, where various HTML elements like an input have a responsive Vuetify counterpart, which is denoted with a “v-“ prefix."),_c('br'),_vm._v("This principle should be considered in the project work of the seminar. For example, it is not advisable to name a variable “x” because it is not clear to other project members what the variable could stand for. This causes a significant decrease in maintainability and productivity. If the project group follows this principle of standardization, it “facilitate[es] communication within multidisciplinary web development teams and leading to agreed designs” (Tena et al., 2013, p. 1).")])],1)],1)}
